<template>
  <div class="final-page">
    <button @click="$router.push({name:'MainView'})" class="close ma-2 pa-2">
      <img src="@/assets/mission/close.png">
    </button>
    <div class="photo">
      <img id="displayImage" alt="photo">
    </div>
    <div class="present">
      <img :src="require('@/assets/open.png')">
    </div>
    <div class="button-container">
      <v-btn @click="downloadImage" class="ok-btn mt-3 pl-10 pr-10" color="#EF8200"></v-btn>
      <v-btn @click="openGoogleForm" class="google-form-button mt-3 pl-10 pr-10" color="#EF8200">  </v-btn>
    </div>
  </div>
  
</template>

<script>
export default {
name: 'FinalView',
data(){
  return {
    familyphoto:null,
  };
},
mounted() {
  let familyphotos = localStorage.getItem('displayphoto')
    // :   src  
  document.getElementById('displayImage').src = familyphotos;
},
methods: {
  addphotoDataUrl(photoDataUrl) {
    this.familyphoto = photoDataUrl;
    console.log("familyphoto updated: ", this.familyphoto);
  },
  downloadImage() {
    //   .
    const image = document.getElementById('displayImage');

    //  URL .
    const imageUrl = image.src;

    //      .
    const link = document.createElement('a');
    link.href = imageUrl;

    //    .
    link.download = 'familyphoto';

    //    .
    document.body.appendChild(link);
    link.click();

    //   .
    document.body.removeChild(link);
    },

  openGoogleForm(){
    const url = "https://docs.google.com/forms/d/e/1FAIpQLSdBCQgSi7xSaxSddm6OTSFwxXKcOjrNvLxfllJq-o0S7_09OQ/viewform?usp=sf_link";
    const options = "width = 700, height=600, left=200, top=200";
    window.open(url, "_blank", options);
    },
},
}

</script>

<style scoped>
.final-page{
  background-image: url('@/assets/background.png');
  background-size: cover; /*  div    */
  height: 100vh; /*     */
  display: flex;
  justify-content: center; /*    */
  align-items: center; /*    */
  position: relative;
}
.close img{
  width: 22px;
  height: 22px;
  cursor: pointer;
  position: absolute;
  top: 3%;
  right: 5%;
}

.photo {
  position: absolute;
  width: 70%;
  max-width: 80%;
  max-height: auto;
  height: auto;
  top: 10%;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
}

.photo img{
  max-width: 100%;
  max-height: auto;

}
.present{
  position: absolute;
  bottom: 18%;
  width: 65%;
  height: auto;
}
.present img{
  width: 100%;
  height: 100%;
  /* box-shadow: 0 0 10px rgba(0, 0, 0, 0.5); */
}
.button-container{
  position: absolute;
  bottom: 3%;
  display: flex; /* Flexbox   */
  flex-direction: column; /*    */
  align-items: center; /*    */
}

.ok-btn, .google-form-button {
  color: white !important;
  font-weight: bold;
  font-size: 18px;
  margin-bottom: 10px; /*    */
  width: 80%;
}

</style>