<template>
    <div class="black-bg" v-if="show">
        <div class="white-bg">
            <img class = 'background' src= '@/assets/mission/bg_4.png'>
            <button class="close" @click="closeP">
                <img src="@/assets/mission/close.png">
            </button>
            <div class="pic">
                <img src="@/assets/photo-bandal.png">
            </div>
            <div class="content">
                <div class="cam-container">
                    <div @click="triggerCamera" class="cam-button mb-3">
                        <v-btn class="custom-submit-button mt-3 pl-10 pr-10" color="#EF8200"> </v-btn>
                    </div>
                    <input type="file" name="photofile" id="photofile" accept="image/*" capture="camera"
                    @change="onPhotoFileChange">
                </div>
                <br>
                <div class="img-container text-center">
                    <img src="" id="photoimg">
                </div>
                <!-- <div class="button-container">
                    <v-btn class="custom-submit-button mt-3 pl-10 pr-10" color="#EF8200" @click="submitResponse"></v-btn>
                </div> -->
            </div>
        </div>
        <v-dialog v-model="dialog" max-width="500">
            <v-card class="pa-3">
                <v-card-text>
                <v-row align="center" justify="center">
                    <v-col align="center">
                    <div class="img-container text-center">
                        <img :src="uploadedPhoto" id="dialogPhoto" style="max-height:100%; max-width:100%;">
                    </div>
                    <br>
                    <div class="text-center">
                        <p>    <strong></strong>  .</p>
                    </div>
                    <div class="button-container">
                        <div class="btn">
                            <v-btn @click="downloadImage" class="download mr-2" color="#EF8200"></v-btn>
                        </div>
                        <div class="next">
                            <v-btn @click="handleDialogConfirmation" class="nextmission" color="#EF8200">  >></v-btn>
                        </div>
                    </div>
                    </v-col>
                </v-row>
                </v-card-text>
            </v-card>
        </v-dialog>
    </div>
</template>

<script >

export default {
    props: {
        show: {
            type: Boolean,
            required: true,
        },
        result4: {
            type: Boolean,
        }
    },
    data(){
      return {
        userResult: '', //   
        dialog: false,

        userResponse:'',
        uploadedPhoto: null, //     
      }
    },
    methods: {
        closeP(){
            this.$emit('close');
        },
        triggerCamera(){
            const photoFileInput = this.$el.querySelector("#photofile");
            photoFileInput.click();
        },
        onPhotoFileChange(event){

            //    .
            const fileReader = new FileReader();
            fileReader.readAsDataURL(event.target.files[0]);
            fileReader.onload = () => {
                
                const photoDataUrl = fileReader.result;
                this.uploadedPhoto = photoDataUrl;

                // this.addPhoto(photoDataUrl);
                this.$store.commit('addPhoto', photoDataUrl);
            }
            this.dialog = true;
        },

        handleDialogConfirmation(){
            this.dialog = false;

            if (this.result4 === false){
                this.$emit('answerCorrect');
            }
            this.userResult = ''; //   
            this.closeP();        //  
        },

        downloadImage() {
            if (this.uploadedPhoto) {
            //    
            const link = document.createElement('a');
            link.href = this.uploadedPhoto;

            //     (: downloaded-image.jpg)
            const filename = 'family_img.jpg';
            link.download = filename;

            //     
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
            } else {
            //      (:    )
            alert('  !');
            }
        },

    }
}
</script>

<style scoped>
    .black-bg{
        width: 100%; height: 100%;
        background: rgba(0, 0, 0, 0.5);
        position: fixed; 
        top: 0;
        left: 0;
        padding: 20px;
        z-index: 1500;
        display: flex;
        align-items: center;
        justify-content: center;
      }

    .img-container{
        justify-content: center;
        display: flex;
        align-items: center;
    }
    #photoimg {
        display: none;
        width: 50%;
        height: auto; /*     */
        margin-top: 20px; /*    */
    }

    #photofile{
        display:None;
    }

    #dialogPhoto {
        width: 80%; /*    */
    }
    .cam-container {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 20px; /*    */
    }
    .button-container {
        width: 100%; /*    */
        padding: 10px 20px; /*    */
        display: flex; /*    */
        justify-content: center; /*   */
        margin-top: auto; /*         */
    }
      .white-bg{
        width: 90%; height: 90%;
        position: fixed; 
        /* background-color: white; */
        overflow:hidden;
        display:flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

      }
    .background{
        width: 100%;
        height: 100%;
    }

    .content{
        width:100%;
        position: absolute;
        bottom: 7%;
        max-height: 45%;
        overflow-y: auto;
    }

    .close img{
        width: 22px;
        height: 22px;
        cursor: pointer;
        justify-content: center;
        align-items: center;
        position:absolute;
        top: 3%;
        right: 5%;
    }
    .custom-submit-button {
        color: white !important; /*    */
        font-weight: bold; /*    */
        font-size: 18px; /*   18px  */
    }
    .next {
        justify-content: center; /*     */
        }
    .download{
        color: white !important; 
        font-weight: bold; 
        font-size: 18px;
        width: 110px;
    }
    .nextmission{
        color: white !important; 
        font-weight: bold; 
        font-size: 18px;
        width: 190px;
    }
    .pic{
        width: auto;
        height: 35%;
        bottom: 17%;
        position: absolute;
        left: 52%;
        transform: translateX(-50%);
    }
    .pic img{
        width: 100%;
        height: 100%;
    }
</style>