<template>
  <div class="main">    
    <l-map
      style="height: 100%; width: 100%"
      :center="center"
      :zoom="zoom"
      :zoomAnimation=true
      :options="mapOptions"
      :maxBounds="maxBounds"
      class="map" 
      ref="map"
      @update:zoom="zoomUpdated"
      @update:center="centerUpdated"
      @update:bounds="boundsUpdated"
    >

    <div class="logo">
      <img :src="require('@/assets/logo.png')">
    </div>

    <div class="mission">
      <img :src="require('@/assets/mission.png')" @click="getDurumari">
      <div class="mission-current">
        {{ completedMissionsCount }}/{{ totalMissions }}
      </div>
      
    </div>
    <v-btn
      elevation="2"
      icon
      :color="isZoom ? 'red' : 'blue'"
      class="my-location-button"
      @click="ZoomInToCurrentPosition"
      height = "33px"
      width = "33px"
      style="border-radius: 50%; background-color: white;"
    >
      <v-icon size = "28">mdi-crosshairs-gps</v-icon>
    </v-btn>

      <m1 :show="popVal1" @close="closeP(1)" @answerCorrect="updateResult(1)" class="popup" :class="{'show':popVal1}" />
      <m2 :show="popVal2" @close="closeP(2)" @answerCorrect="updateResult(2)" class="popup" :class="{'show':popVal2}" />
      <m3 :show="popVal3" @close="closeP(3)" @answerCorrect="updateResult(3)" class="popup" :class="{'show':popVal3}" />
      <m4 :show="popVal4" @close="closeP(4)" @answerCorrect="updateResult(4)" class="popup" :class="{'show':popVal4}" />
      <m5 :show="popVal5" @close="closeP(5)" @answerCorrect="updateResult(5)" class="popup" :class="{'show':popVal5}" />
      <m6 :show="popVal6" @close="closeP(6)" @answerCorrect="updateResult(6)" class="popup" :class="{'show':popVal6}" />
      <m7 :show="popVal7" @close="closeP(7)" @answerCorrect="updateResult(7)" class="popup" :class="{'show':popVal7}" />
      <m8 :show="popVal8" @close="closeP(8)" @answerCorrect="updateResult(8)" class="popup" :class="{'show':popVal8}" />
      <m9 :show="popVal9" @close="closeP(9)" @answerCorrect="updateResult(9)" class="popup" :class="{'show':popVal9}" />
      <m10 :show="popVal10" @close="closeP(10)" @answerCorrect="updateResult(10)" class="popup" :class="{'show':popVal10}" />

      <InfoChiakView :show="showInfoChiak" class="infoChiak" :class="{'show': showInfoChiak}" @closeTutorial="closed"/>
      <GiftView :show="allResValue" :class="{'show': allRes}" @closeGift="closeLast" ref="GiftView"/>
      
      <LMarker
        :lat-lng="mainplacemarkers[0].coordinates"
        :icon="placeICON1" />
      <LMarker
        :lat-lng="mainplacemarkers[1].coordinates"
        :icon="placeICON2" />
      <LMarker
        :lat-lng="mainplacemarkers[2].coordinates"
        :icon="placeICON3" />
      <LMarker
        :lat-lng="mainplacemarkers[3].coordinates"
        :icon="placeICON4" />
      <LMarker
        :lat-lng="mainplacemarkers[4].coordinates"
        :icon="placeICON5" />
      <LMarker
        :lat-lng="mainplacemarkers[5].coordinates"
        :icon="placeICON6" />
      <LMarker
        :lat-lng="mainplacemarkers[6].coordinates"
        :icon="placeICON7" />

      <LMarker
        :key="markers[0].id"
        :lat-lng="markers[0].coordinates"
        @click="openP(0)"
        :icon="result1 ? customIcon: defaultIcon" />

      <LMarker
        :key="markers[1].id"
        :lat-lng="markers[1].coordinates"
        @click="openP(1)"
        :icon="result2 ? customIcon: defaultIcon" 
        v-if="result1 === true" />

      <LMarker
        :key="markers[2].id"
        :lat-lng="markers[2].coordinates"
        @click="openP(2)"
        :icon="result3 ? customIcon: defaultIcon" 
        v-if="result2 === true" />

      <LMarker
        :key="markers[3].id"
        :lat-lng="markers[3].coordinates"
        @click="openP(3)"
        :icon="result4 ? customIcon: defaultIcon"
        v-if="result3 === true" />

      <LMarker
        :key="markers[4].id"
        :lat-lng="markers[4].coordinates"
        @click="openP(4)"
        :icon="result5 ? customIcon: defaultIcon" 
        v-if="result4 === true" />
      
      <LMarker
        :key="markers[5].id"
        :lat-lng="markers[5].coordinates"
        @click="openP(5)"
        :icon="result6 ? customIcon: defaultIcon" 
        v-if="result5 === true" />

      <LMarker
        :key="markers[6].id"
        :lat-lng="markers[6].coordinates"
        @click="openP(6)"
        :icon="result7 ? customIcon: defaultIcon" 
        v-if="result6 === true" />

      <LMarker
        :key="markers[7].id"
        :lat-lng="markers[7].coordinates"
        @click="openP(7)"
        :icon="result8 ? customIcon: defaultIcon" 
        v-if="result7 === true" />

      <LMarker
        :key="markers[8].id"
        :lat-lng="markers[8].coordinates"
        @click="openP(8)"
        :icon="result9 ? customIcon: defaultIcon" 
        v-if="result8 === true" />

      <LMarker
        :key="markers[9].id"
        :lat-lng="markers[9].coordinates"
        @click="openP(9)"
        :icon="result10 ? customIcon: defaultIcon" 
        v-if="result9 === true" />

      <l-circle :lat-lng="currentPos" :radius=circle.radius :color=circle.color />

      <div class="navi-bar">
        <v-btn
          elevation="2"
          icon
          color="teal"
          @click="showTutorialPopup" 
          class="story"
          height = "50px"
          width = "50px"
          style="border-radius: 50%; background-color: white;">
          <v-icon size = "28">mdi-book-open-page-variant-outline</v-icon>
        </v-btn>
        <v-btn
          elevation="2"
          icon
          color="teal"
          @click="getGallery" 
          class="gallery"
          height = "50px"
          width = "50px"
          style="border-radius: 50%; background-color: white;">
          <v-icon size = "28">mdi-image-multiple</v-icon>
        </v-btn>
        <v-btn
          elevation="2"
          icon
          color="teal"
          @click="InfoChiak" 
          class="credit"
          height = "50px"
          width = "50px"
          style="border-radius: 50%; background-color: white;">
          <v-icon size = "28">mdi-text-box-check-outline</v-icon>
        </v-btn>
        <v-btn
          elevation="2"
          icon
          color="teal"
          @click="$router.push({name: 'AIView'})" 
          class="uploadImg"
          height = "80px"
          width = "80px"
          style="border-radius: 50%; background-color: white;">

          <v-icon size = "50">mdi-panorama-variant-outline</v-icon>
        </v-btn>

        <!-- <div class="preview"  v-show="previewVisible">
          <img :src="require('@/assets/preview.png')">
        </div> -->
      </div>
        
      <l-tile-layer :url="url" />

        <TutorialView :show="showtutorial" 
            @closeTutorial="closeTutorial"
            @openTutorial="showTutorialPopup" />
        <Durumari :show="isCredit" @close="closeDurumari"/>
        <GalleryView :show="gallery_open" @close="closeGallery" />
        <UploadImageView :show="uploadIMG" :class="{'show':uploadIMG}" @close="closeUImg"/>

      <div  class="animated-marker" v-show="isGift">
        <img :src="require('@/assets/present.png')" 
        @click="$router.push({name: 'FinalView'})">
      </div>

    </l-map>
  </div>
</template>
   
<script>
import { LMap, LTileLayer, LMarker, LCircle } from 'vue2-leaflet';
import L from 'leaflet';
import { Icon } from 'leaflet';

import m1 from '../components/mission/m1View.vue';
import m2 from '../components/mission/m2View.vue';
import m3 from '../components/mission/m3View.vue';
import m4 from '../components/mission/m4View.vue';
import m5 from '../components/mission/m5View.vue';
import m6 from '../components/mission/m6View.vue';
import m7 from '../components/mission/m7View.vue';
import m8 from '../components/mission/m8View.vue';
import m9 from '../components/mission/m9View.vue';
import m10 from '../components/mission/m10View.vue';

import GiftView from '../components/GiftView.vue';

import 'leaflet/dist/leaflet.css';
import 'leaflet-gpx';

import markerImg from 'leaflet/dist/images/marker-icon.png';
import markerShadowImg from 'leaflet/dist/images/marker-shadow.png';
import markerRetinaImg from 'leaflet/dist/images/marker-icon-2x.png';

import Durumari from './NaviView/DurumariView.vue';
import GalleryView from './NaviView/GalleryView.vue';
import TutorialView from './NaviView/TutorialView.vue';
import UploadImageView from './NaviView/UploadImageView2.vue';
import InfoChiakView from './NaviView/InfoChiakView.vue';


//     
function saveResultsToLocalStorage(results) {
  sessionStorage.setItem('missionResults', JSON.stringify(results));
}

//     
function getResultsFromSessionStorage() {
  const resultsJSON = sessionStorage.getItem('missionResults');
  return resultsJSON ? JSON.parse(resultsJSON) : {};
}

export default {

  data () {
    return {
      url: 'https://{s}.tile.openstreetmap.fr/hot/{z}/{x}/{y}.png',
      attribution: '&copy; <a href="https://www.hotosm.org/" target="_blank">Humanitarian OpenStreetMap Team</a> '
                    + '&copy; <a href="https://openstreetmap.org">OpenStreetMap</a> contributors',
      center: [37.4148974953341, 128.050171136856],
      zoom: 16, //   
      previousZoom : null, //  
      bounds: null, //     ( /  )
      maxBounds:[
        [37.3721675168233, 128.0170751794774], // 
        [37.46310508072958, 128.0814481958348] // 
      ],

      popVal1 : false, //pop1    (default=false)
      popVal2 : false,
      popVal3 : false,
      popVal4 : false,
      popVal5 : false,
      popVal6 : false,
      popVal7 : false,
      popVal8 : false,
      popVal9 : false,
      popVal10 : false,

      result1: false,
      result2: false,
      result3: false,
      result4: false,
      result5: false,
      result6: false,
      result7: false,
      result8: false,
      result9: false,
      result10: false,

      allResValue: false,

      isFirstMissionOpened: false, //    

      isZoomIn: false, //    (   )
      isZoom : false, //       
      isGift: false, //      (  + )  
      current : [0,0], // ZoomInToCurrentPosition        
      currentPos: [0,0], //      
      showInfoChiak: false,

      isCredit: false,
      gallery_open: false,
      showtutorial: false,
      uploadIMG: false,

      isPositionReady : false,
      positionObj: {
        latitude: 0,
        longitude: 0
      },

      mapOptions: {
        minZoom: 13, //   
        maxZoom: 18,  //   
        zoomControl: false, //     (  ->  )
      },

      markers: [
        {id:1, coordinates: [37.4148974953341, 128.050171136856], name:'   '},
        {id:2, coordinates: [37.4047839823339, 128.047338724136], name:''},
        {id:3, coordinates: [37.40485112, 128.04897584], name:' '},
        {id:4, coordinates: [37.403465098361, 128.048915863037], name:'1: '},
        {id:5, coordinates: [37.40172059, 128.05039145], name:' 2 :   '},
        {id:6, coordinates: [37.3992934, 128.0498706], name:''},
        {id:7, coordinates: [37.39793653, 128.05143569], name:' '},
        {id:8, coordinates: [37.3972176621091, 128.051402270794], name:'   '},
        {id:9, coordinates: [37.3962118870025, 128.051946759224], name:' '},
        {id:10, coordinates: [37.3943772,128.05353051], name:'()'},
      ],
      //   , 
      mainplacemarkers: [
        {id:1, coordinates: [37.415178702643,128.050192594528], name:""},
        {id:2, coordinates: [37.408346344485,128.044876456261], name:""},
        {id:3, coordinates: [37.4050247453998,128.047234117985], name:""},
        {id:4, coordinates: [37.405188804745, 128.049248456955], name:""},
        {id:5, coordinates: [37.3996361550487, 128.049114346504], name:""},
        {id:6,coordinates: [37.3949972677172, 128.053389787674], name:""},
        {id:7, coordinates: [37.3942173439044, 128.053000867367], name:""},
        {id:8, coordinates: [37.3941725939312, 128.054344654083], name:"()"},
      ],
      placeICON1: new Icon({
        iconUrl: require('@/assets/mainplace/.png'),
        iconSize: [80, 80],
        iconAnchor: [16,32]
      }),
      placeICON2: new Icon({
        iconUrl: require('@/assets/mainplace/.png'),
        iconSize: [80, 80],
        iconAnchor: [16,32]
      }),
      placeICON3: new Icon({
        iconUrl: require('@/assets/mainplace/.png'),
        iconSize: [90, 80],
        iconAnchor: [16,32]
      }),
      placeICON4: new Icon({
        iconUrl: require('@/assets/mainplace/.png'),
        iconSize: [80, 80],
        iconAnchor: [16,32]
      }),
      placeICON5: new Icon({
        iconUrl: require('@/assets/mainplace/.png'),
        iconSize: [80, 80],
        iconAnchor: [16,32]
      }),
      placeICON6: new Icon({
        iconUrl: require('@/assets/mainplace/.png'),
        iconSize: [80, 80],
        iconAnchor: [16,32]
      }),
      placeICON7: new Icon({
        iconUrl: require('@/assets/mainplace/.png'),
        iconSize: [90, 80],
        iconAnchor: [16,32]
      }),
      placeICON8: new Icon({
        iconUrl: require('@/assets/mainplace/().png'),
        iconSize: [100, 90],
        iconAnchor: [16,32]
      }),
      defaultIcon: new Icon({  //     (  )
        iconUrl: require('@/assets/marker.png'),
        iconSize: [50, 50],
        iconAnchor: [16,32]
      }),
      customIcon: new Icon({  //    
        iconUrl: require('@/assets/.png'),
        iconSize: [60, 60],
        iconAnchor: [16,32]
      }),
      circle: { radius: 5, color: 'blue'}
    };
  },
  components: {
    LMap,
    LTileLayer,
    LMarker,
    LCircle,
    m1,
    m2,
    m3,
    m4,
    m5,
    m6,
    m7,
    m8,
    m9,
    m10,
    InfoChiakView,
    GiftView,
    Durumari,
    GalleryView,
    TutorialView,
    UploadImageView,
  },
  mounted(){
    delete Icon.Default.prototype._getIconUrl;
    Icon.Default.mergeOptions({ //marker-icon-2x.png      
      iconRetinaUrl: markerRetinaImg,
      iconUrl: markerImg,
      shadowUrl: markerShadowImg,
    });

    this.$refs.map.mapObject.attributionControl.setPrefix(''); // attribution  

    // gpx   
    this.loadGPX();

    //    
    this.getCurrentPosition();
    this.currentPosIcon = L.icon({
      iconUrl: require('@/assets/logo.png'),
      iconSize: [16,16],
      iconAnchor: [16,32]
    });
  },
  methods: {
    getDurumari(){
      this.isCredit = true;
      this.gallery_open = false;
      this.showtutorial = false;
      this.uploadIMG = false;
    },
    closeDurumari(){
      this.isCredit = false;
      this.gallery_open = false;
      this.showtutorial = false;
      this.uploadIMG = false;
    },
    getGallery(){
      this.gallery_open = true;
      this.showtutorial = false;
      this.uploadIMG = false;
      this.isCredit = false;
      if (this.$refs.map && this.$refs.map.mapObject) {
          this.$refs.map.mapObject.dragging.disable(); //      
          this.$refs.map.mapObject.scrollWheelZoom.disable(); //    /  
        }
    },
    closeGallery(){
      this.gallery_open = false;
      this.isCredit = false;
      this.showtutorial = false;
      this.uploadIMG = false;
      if (this.$refs.map && this.$refs.map.mapObject) {
        this.$refs.map.mapObject.dragging.enable();
        this.$refs.map.mapObject.scrollWheelZoom.enable();
      }
    },
    closeTutorial(){
      this.isCredit = false;
      this.gallery_open = false;
      this.showtutorial = false;
      this.uploadIMG = false;
      if (this.$refs.map && this.$refs.map.mapObject) {
        this.$refs.map.mapObject.dragging.enable();
        this.$refs.map.mapObject.scrollWheelZoom.enable();
      }
    },
    showTutorialPopup(){
      this.showtutorial = true;
      this.uploadIMG = false;
      this.isCredit = false;
      this.gallery_open = false;
      if (this.$refs.map && this.$refs.map.mapObject) {
          this.$refs.map.mapObject.dragging.disable(); //      
          this.$refs.map.mapObject.scrollWheelZoom.disable(); //    /  
        }

    },
    shwoUImg(){
      this.uploadIMG = true;
      this.showtutorial = false;
      this.isCredit = false;
      this.gallery_open = false;
    },
    closeUImg(){
      this.isCredit = false;
      this.gallery_open = false;
      this.showtutorial = false;
      this.uploadIMG = false;
    },
    closeModal(){
      this.showModal = false;
    },

    zoomUpdated (zoom) {
      this.zoom = zoom;
    },
    centerUpdated (center) {
      this.center = center;
    },
    boundsUpdated(bounds) {
      this.bounds = bounds;
    },
    openP(index){
      if(! this.isZoomIn){
        this.previousZoom = this.zoom;
        //    
        const marker = this.markers[index];
        this.$refs.map.mapObject.setZoomAround(marker.coordinates, 36);
        this.isZoomIn = true;
      }
      // 0.8   
      setTimeout(()=>{this['popVal' + (index+1)] = true;}, 800);

      //     
      if (this.$refs.map && this.$refs.map.mapObject) {
      this.$refs.map.mapObject.dragging.disable(); //      
      this.$refs.map.mapObject.scrollWheelZoom.disable(); //    /  
    }
    if(index === 0){ //   preview 
      this.isFirstMissionOpened = true;
    }

    },
    closeP(idx){
      this.popVal1 = false;
      this.popVal2 = false;
      this.popVal3 = false;
      this.popVal4 = false;
      this.popVal5 = false;
      this.popVal6 = false;
      this.popVal7 = false;
      this.popVal8 = false;
      this.popVal9 = false;
      this.popVal10 = false;
      
      //     
      if (this.$refs.map && this.$refs.map.mapObject) {
      this.$refs.map.mapObject.dragging.enable();
      this.$refs.map.mapObject.scrollWheelZoom.enable();
      }

    setTimeout(() => {
      if (this.isZoomIn && idx < 10 && idx > 1) {
        //     (   )
        const lat_ = (this.markers[idx-1].coordinates[0] + this.markers[idx].coordinates[0])/2;
        const lng_ = (this.markers[idx-1].coordinates[1] + this.markers[idx].coordinates[1])/2;
        this.$refs.map.mapObject.panTo([lat_, lng_], { duration: 0.3 });
        this.$refs.map.mapObject.setView([lat_, lng_], this.previousZoom);
        this.isZoomIn = false;
      } else if (this.isZoomIn && idx === 1){
        this.$refs.map.mapObject.panTo(this.markers[idx].coordinates, { duration: 0.3 });
        this.$refs.map.mapObject.setView(this.markers[idx].coordinates, 15);
        this.isZoomIn = false;
      }
    }, 300);

      if (idx === 10){
        this.allRes();
        this.$refs.map.mapObject.setView(this.markers[4].coordinates, 15);
      }
    }, 

    getPositionValue(pos) {
      this.positionObj.latitude = pos.coords.latitude;
      this.positionObj.longitude = pos.coords.longitude;
      this.currentPos = [this.positionObj.latitude, this.positionObj.longitude];
      this.isPositionReady = true;
       
      if (this.$refs.map && this.$refs.map.mapObject) {
        //     (     )
        if(this.marker){
          this.$refs.map.mapObject.removeLayer(this.marker)
        }
      }
    },
    getCurrentPosition(){
      if(!navigator.geolocation){
        alert('    .')
      } else {
        navigator.geolocation.watchPosition(this.getPositionValue, 
        ()=>alert('    .2')) //     getPositionValue,     
      }
    },

    ZoomInToCurrentPosition(){
      this.current = this.currentPos;
      if (this.isZoom) {
        this.$refs.map.mapObject.setView(this.current, this.Zoom);
        this.isZoom = false;
      } else {
        this.$refs.map.mapObject.setView(this.currentPos, 16);
        this.isZoom = true;
      }
    },

    goToCenter(){
      this.$refs.map.mapObject.setView([37.408473, 128.045787],14);
    },
    loadGPX(){
      //.gpx     
      const gpxUrl = process.env.BASE_URL + '1108  GPS.gpx'
      fetch(gpxUrl)
      .then(response => response.text())
      .then(data => {
        // GPX  
        const gpx = new DOMParser().parseFromString(data, 'text/xml');
        const trackCoords = [];
        
        //   
        const trkseg = gpx.getElementsByTagName('trkpt');
        for (let i = 0; i < trkseg.length; i++) {
          const lat = parseFloat(trkseg[i].getAttribute('lat'));
          const lon = parseFloat(trkseg[i].getAttribute('lon'));
          trackCoords.push([lat, lon]);
        }
        
        //  () 
        const polyline = L.polyline(trackCoords, { color: '#C4DEFF', weight: 15, opacity: 0.8 });

        //   
        polyline.addTo(this.$refs.map.mapObject);

        //     (  )
        // this.$refs.map.mapObject.fitBounds(polyline.getBounds());
        this.$refs.map.mapObject.setView(this.center, this.zoom);
      })
      .catch(error => {
        console.error('Error loading GPX:', error);
      });
    },

    updateResult(id){
      this['result' + (id)]= true;
      const results = {
        result1: this.result1,
        result2: this.result2,
        result3: this.result3,
        result4: this.result4,
        result5: this.result5,
        result6: this.result6,
        result7: this.result7,
        result8: this.result8,
        result9: this.result9,
        result10: this.result10,
      };
      saveResultsToLocalStorage(results);
    },
    InfoChiak(){
      this.showInfoChiak = true;
    },
    
    closed(){
      this.showInfoChiak = false;
      this.uploadIMG = false;
    },
    closeLast(){
      this.allResValue = false;

      this.isGift = true;
    },
    allRes(){
      if(this.result1 && this.result2 && this.result3 && this.result4 && this.result5 && this.result6 && this.result7 && this.result8 && this.result9 && this.result10){
        this.allResValue = true;
      }
      return this.allResValue
    },
    goPage(pageN){
      //  
      this.$router.push({name: pageN});
    }
  },
  computed:{
    completedMissionsCount() {
      return [this.result1, this.result2, this.result3, this.result4, this.result5, this.result6, this.result7, this.result8, this.result9, this.result10].filter(Boolean).length;
    },
    totalMissions() {
      return 10; //   
    },
    previewVisible() {
      //        true   .
      return !this.isFirstMissionOpened;
  }
  },
  created(){
    const storedResults = getResultsFromSessionStorage();
    for (const key in storedResults) {
      if (Object.hasOwnProperty.call(storedResults, key)) {
        this[key] = storedResults[key];
      }
    }
  }
}
</script>

<style scoped>
  .animated-marker img{
    width: 100%;
