<template>
    <div class="black-bg" v-if="show">
        <div class="white-bg">
            <img class = 'background' src= '@/assets/mission/bg_1.png'>
            <button class="close" @click="closeP">
                <img src="@/assets/mission/close.png">
            </button>
            <div class="content-container" justify="center" align="center">
                <v-row class="sound-item d-flex">
                    <v-col cols="4">
                        <v-btn @click="toggleAudio(sounds[0])" fab>
                            <v-icon large  :style="{ color: sounds[0].isActive ? 'green' : 'gray' }">mdi mdi-volume-high</v-icon>
                        </v-btn>
                    </v-col>
                    <v-col cols="4">
                        <v-btn @click="toggleAudio(sounds[1])" fab>
                            <v-icon large  :style="{ color: sounds[1].isActive ? 'green' : 'gray' }">mdi mdi-volume-high</v-icon>
                        </v-btn>
                    </v-col>
                    <v-col cols="4">
                        <v-btn @click="toggleAudio(sounds[2])" fab>
                            <v-icon large  :style="{ color: sounds[2].isActive ? 'green' : 'gray' }">mdi mdi-volume-high</v-icon>
                        </v-btn>
                    </v-col>
                </v-row>
                <v-row class="sound-item">
                    <v-col cols="auto" >
                        <v-checkbox hide-details class="shrink ml-4 mr-2" style="transform: scale(1.5);" v-model="userResult" :value="sounds[0].value"></v-checkbox>
                    </v-col>
                    <v-col cols="auto" >
                        <v-checkbox hide-details class="shrink ml-4 mr-2" style="transform: scale(1.5);" v-model="userResult" :value="sounds[1].value"></v-checkbox>
                    </v-col>
                    <v-col cols="auto" >
                        <v-checkbox hide-details class="shrink ml-4 mr-2" style="transform: scale(1.5);" v-model="userResult" :value="sounds[2].value"></v-checkbox>
                    </v-col>
                </v-row>
                <br>
                <br>
                <br>
                <v-btn class="custom-submit-button" color="#EF8200" @click="submitSoundRes"></v-btn>
            </div>

        </div>
        <v-dialog v-model="dialog" max-width="500">
            <v-card>
                <v-card-text>
                    <v-row align="center" justify="center">
                        <v-col align="center">
                            <div style="height:100%; width:100%; display: flex; align-items: center; justify-items: center;">
                                <v-img v-if="userResult === ''" src="@/assets/O.png"></v-img>
                                <v-img v-else src="@/assets/X.png"></v-img>
                            </div>
                            <br>
                            <div v-if="userResult === ''" class="text-center">
                                <h3>!</h3> 
                                <br>
                                <br>
                                <h4>     ? </h4>
                                <br>
                                <h4>      </h4>
                                <h4> ! </h4>
                                <br>
                            </div>
                            <div v-else class="text-center">
                                <h3>  ?</h3>
                            </div>
                        </v-col>
                    </v-row>
                </v-card-text>
                <v-card-actions class="card">
                <v-btn @click="handleDialogConfirmation(userResult === '')" class="ok-btn" color="#EF8200">{{ userResult === '' ? ' >>' : '' }}</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </div>
</template>

<script scoped>
import { Howl } from 'howler';

export default {
    props: {
        show: {
            type: Boolean,
            required: true,
        },
        result1: {
            type: Boolean,
        }
    },
    data(){
      return {
            userResult: '', //   
            showPopup: false, // 
            dialog: false,

            sounds: [
                { id: 1, src: "/.wav", isActive: false, howl: null, value: '' },
                { id: 2, src: "/.mp3", isActive: false, howl: null, value: '' },
                { id: 3, src: "/.wav", isActive: false, howl: null, value: '' },
            ],
      }
    },
    methods: {
        closeP(){
            this.$emit('close');
        },
        submitResponse(){
          console.log(this.userResponse); // ,     
          this.userResponse = ''; //  
        },
        //  
        showHint(){
          this.showPopup=true;
        },
        //  
        closePopup(){
          this.showPopup=false;
        },
        submitSoundRes(){
            this.stopAllSounds();
            if(this.userResult.trim() == ''){
                alert(" !")
            } else {
                if(this.userResult === ''){
                    //   
                    this.dialog = true;
                } else {
                    //  
                    this.dialog = true;
                }
            }
        },
        stopAllSounds() {
            this.sounds.forEach((sound) => {
                if (sound.playing) {
                    sound.howl.stop();
                    sound.playing = false;
                    sound.isActive = false;
                }
            });
        },
        handleDialogConfirmation(correct){
            this.dialog = false;
            if(correct){
                if (this.result1 === false){
                    //result1 false  'answerCorrect'  (    )
                    this.$emit('answerCorrect');
                }
                this.userResult = ''; //   
                this.closeP();        //  
            }
        },
        toggleAudio(sound) {
            this.stopAllSoundsExcept(sound.id);

            if (sound.playing) {
                sound.howl.stop();
            } else {
                sound.howl = new Howl({
                src: [sound.src],
                volume: 1,
                onend: () => {
                    sound.playing = false;
                },
                });
                sound.howl.play();
                sound.playing = true;
            }

            //    
            sound.isActive = !sound.isActive;
        },
        stopAllSoundsExcept(exceptId) {
            this.sounds.forEach((sound) => {
                if (sound.playing && sound.id !== exceptId) {
                sound.howl.stop();
                sound.playing = false;
                sound.isActive = false; //     isActive false 
                }
            });
        },

    }
}
</script>

<style scoped>
    img{
        width: 100%;
    }
    .black-bg{
        width: 100%; height: 100%;
        background: rgba(0, 0, 0, 0.5);
        position: fixed; 
        top: 0;
        left: 0;
        padding: 20px;
        z-index: 1500;
        display: flex;
        align-items: center;
        justify-content: center;
      }
      .white-bg{
        width: 90%; height: 90%;
        position: fixed; 
        /* background-color: white; */
        overflow:hidden;
        display:flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

      }
    .background{
        width: 100%;
        height: 100%;
    }
    .content-container{
        width:100%;
        position: absolute;
        bottom: 15%;
    }
    .close{
        width: 22px;
        height: 22px;
        cursor: pointer;
        justify-content: center;
        align-items: center;
        position:absolute;
        top: 3%;
        right: 5%;
    
    }
    .custom-submit-button {

        color: white !important; /*    */
        font-weight: bold; /*    */
        font-size: 18px; /*   18px  */
    }
    .card {
    justify-content: center; /*     */
    }
    .ok-btn{
        color: white !important; 
        font-weight: bold; 
        font-size: 18px;
    }
    .center-checkbox {
    display: flex;
    justify-content: center;
    align-items: center;
  }

.v-application .justify-center {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
}
